@import url('./assets/css/font.css');

* {
  margin: 0;
  padding: 0;
}

html {
  background: #090a13;
  background-image: url('./assets/images/main-bg.png');
  background-repeat: no-repeat;
  background-size: 100% auto;
}

li {
  list-style: none;
}

.clearfix:after {
  content: '';
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.clearfix {
  *zoom: 1;
}

.App {
  text-align: center;
}

.disable {
  cursor: not-allowed;
}

.main {
  .wrap {
    width: 1280px;
    margin: 0 auto;
  }

  // header
  .header {
    color: #ffffff;
    width: 100%;
    margin-top: 56px;

    h1 {
      width: 340px;
      float: left;

      img {
        width: 100%;
      }
    }

    ul {
      float: left;
      margin-left: 28px;
      margin-top: 57px;

      li {
        float: left;
        cursor: pointer;
        list-style: none;
        font-size: 24px;
        font-family: Gilroy-Medium, Gilroy;
        font-weight: 500;
        color: #ffffff;
        line-height: 28px;
        margin-left: 50px;
      }
    }

    button {
      float: right;
      cursor: pointer;
      padding: 20px;
      font-size: 24px;
      font-family: Gilroy-Regular, Gilroy;
      font-weight: 400;
      color: #090a13;
      line-height: 28px;
      background: #ffffff;
      border-radius: 10px;
      border: none;
      margin-top: 35px;
    }
  }

  // banner

  .banner {
    height: 570px;
    background-image: url('./assets/images/banner.png');
    background-size: auto;
    background-repeat: no-repeat;
    background-position: right;
    margin-top: 45px;
    padding-left: 116px;

    .banner-title {
      width: 596px;
      margin-top: 96px;

      img {
        width: 100%;
      }
    }

    .banner-text {
      font-size: 48px;

      font-family: Gilroy-Medium, Gilroy;
      font-weight: 500;
      color: #ffffff;
      line-height: 80px;
      padding-top: 40px;
      text-shadow: -12px 10px 7px grey



    }

    .contact {
      ul {
        margin-top: 59px;

        li {
          margin-right: 16px;
          float: left;

          img {
            width: 240px;
          }
        }

        img:hover {
          -webkit-animation: burn 1000ms ease-out forwards;
          animation: burn 1000ms ease-out forwards;
          border-radius: 5px;
          background: black;
        }

        img:hover::before {
          content: '';
          left: 100%;
          width: 100%;
          height: 100%;
          // background: #ffff;
          border-radius: 999px;
          -webkit-animation: flare 1000ms ease-out forwards;
          animation: flare 1000ms ease-out forwards;
        }

        img:hover::after {
          content: '';
          right: 100%;
          width: 100%;
          height: 100%;
          // background: #ffff;
          border-radius: 999px;
          -webkit-animation: flare 1000ms ease-out forwards;
          animation: flare 1000ms ease-out forwards;
        }

      }
    }
  }

  // banner-text
  .banner-content {
    font-size: 48px;
    height: 200px;

    .content {
      text-align: center;
      margin-top: 100px;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      overflow: hidden;

      font-family: 'Russo One';
      font-size: 35px;
      line-height: 40px;
      color: #ecf0f1;

      &__container {
        font-weight: 600;
        overflow: hidden;
        height: 40px;
        padding: 0 40px;


        &:before {
          content: '[';
          left: 0;
        }

        &:after {
          content: ']';
          position: absolute;
          right: 0;
        }

        &:after,
        &:before {
          position: absolute;
          top: 0;

          color: #f98518;
          font-size: 42px;
          line-height: 40px;

          -webkit-animation-name: opacity;
          -webkit-animation-duration: 2s;
          -webkit-animation-iteration-count: infinite;
          animation-name: opacity;
          animation-duration: 2s;
          animation-iteration-count: infinite;
        }

        &__text {
          display: inline;
          float: left;
          margin: 0;
        }

        &__list {
          margin-top: 0;
          padding-left: 165px;
          text-align: left;
          list-style: none;

          -webkit-animation-name: change;
          -webkit-animation-duration: 10s;
          -webkit-animation-iteration-count: infinite;
          animation-name: change;
          animation-duration: 10s;
          animation-iteration-count: infinite;

          &__item {
            line-height: 40px;
            margin: 0;
          }
        }
      }
    }

    @-webkit-keyframes opacity {

      0%,
      100% {
        opacity: 0;
      }

      50% {
        opacity: 1;
      }
    }

    @-webkit-keyframes change {

      0%,
      12.66%,
      100% {
        transform: translate3d(0, 0, 0);
      }

      16.66%,
      29.32% {
        transform: translate3d(0, -25%, 0);
      }

      33.32%,
      45.98% {
        transform: translate3d(0, -50%, 0);
      }

      49.98%,
      62.64% {
        transform: translate3d(0, -75%, 0);
      }

      66.64%,
      79.3% {
        transform: translate3d(0, -50%, 0);
      }

      83.3%,
      95.96% {
        transform: translate3d(0, -25%, 0);
      }
    }

    @-o-keyframes opacity {

      0%,
      100% {
        opacity: 0;
      }

      50% {
        opacity: 1;
      }
    }

    @-o-keyframes change {

      0%,
      12.66%,
      100% {
        transform: translate3d(0, 0, 0);
      }

      16.66%,
      29.32% {
        transform: translate3d(0, -25%, 0);
      }

      33.32%,
      45.98% {
        transform: translate3d(0, -50%, 0);
      }

      49.98%,
      62.64% {
        transform: translate3d(0, -75%, 0);
      }

      66.64%,
      79.3% {
        transform: translate3d(0, -50%, 0);
      }

      83.3%,
      95.96% {
        transform: translate3d(0, -25%, 0);
      }
    }

    @-moz-keyframes opacity {

      0%,
      100% {
        opacity: 0;
      }

      50% {
        opacity: 1;
      }
    }

    @-moz-keyframes change {

      0%,
      12.66%,
      100% {
        transform: translate3d(0, 0, 0);
      }

      16.66%,
      29.32% {
        transform: translate3d(0, -25%, 0);
      }

      33.32%,
      45.98% {
        transform: translate3d(0, -50%, 0);
      }

      49.98%,
      62.64% {
        transform: translate3d(0, -75%, 0);
      }

      66.64%,
      79.3% {
        transform: translate3d(0, -50%, 0);
      }

      83.3%,
      95.96% {
        transform: translate3d(0, -25%, 0);
      }
    }

    @keyframes opacity {

      0%,
      100% {
        opacity: 0;
      }

      50% {
        opacity: 1;
      }
    }

    @keyframes change {

      0%,
      12.66%,
      100% {
        transform: translate3d(0, 0, 0);
      }

      16.66%,
      29.32% {
        transform: translate3d(0, -25%, 0);
      }

      33.32%,
      45.98% {
        transform: translate3d(0, -50%, 0);
      }

      49.98%,
      62.64% {
        transform: translate3d(0, -75%, 0);
      }

      66.64%,
      79.3% {
        transform: translate3d(0, -50%, 0);
      }

      83.3%,
      95.96% {
        transform: translate3d(0, -25%, 0);
      }
    }
  }

  .banner-bottom {
