    }


    .event-nav {
      margin-top: 74px;

      ul {
        overflow: hidden;
        border-radius: 20px 10px 0 0;

        li {
          width: 293px;
          height: 80px;
          padding-left: 100px;
          float: left;
          background: #484848;
