  //qwa
  .qwa {
    margin-top: 100px;

    .dialogue {
      float: left;
      background: #292727;
      position: relative;
      width: auto;
      height: auto;
      margin: 10px 0;
      box-sizing: border-box;
      font-size: 14px;
      padding: 25px;
      box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
      font-size: 24px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffe383;
      line-height: 33px;
      border-radius: 15px 15px 15px 0px;

    }

    .dialogue:after {
      position: absolute;
      content: '';
      width: 0;
      height: 0;
      border-color: red transparent;
      border-width: 0 0 15px 15px;
      border-style: solid;
    }

    .dialogue:after {
      border-color: transparent transparent transparent #292727;
      left: 0px;
      bottom: -15px;
      transform: rotate(00deg);
    }

    .dialogue-right {
      background: #ffffff;
      float: right;

      color: #000000;
      border-radius: 15px 15px 0px 15px;

      p {
        line-height: 50px;
      }
    }

    .dialogue-right:after {
      left: auto;
      right: 0;
      border-color: transparent transparent transparent #ffffff;
      transform: rotate(90deg);
    }
  }

  //nft

  .nft-box {
    margin-top: 123px;
    position: relative;
    height: 1550px;

    .nft-title {
      width: 392px;

      img {
        width: 100%;
      }
    }

    .nft-img {
      .img-top {
        position: absolute;
        top: -50px;
        right: 50px;
        z-index: 2;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: box-shadow, transform;
        transition-property: box-shadow, transform;
      }

      .img-top:hover,
      .img-top:focus,
      .img-top:active {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
      }


      .img-bottom {
        position: absolute;
        left: 100px;
        top: 200px;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: box-shadow, transform;
        transition-property: box-shadow, transform;
      }

      .img-bottom:hover,
      .img-bottom:focus,
      .img-bottom:active {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
      }
    }

    .nft-text {
      position: absolute;
      bottom: 100px;
      text-align: center;
      width: 100%;

      p {
        font-size: 36px;
        font-family: 'Russo One';
        font-weight: 600;
        color: #ffffff;
        line-height: 74px;

        span {
          color: #ffe383;
        }

        img {
          width: 70px;
          height: 70px;
          position: absolute;
          margin-left: 30px;

          -webkit-transform: perspective(1px) translateZ(0);
          transform: perspective(1px) translateZ(0);
          -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
          -webkit-transition-property: box-shadow, transform;
          transition-property: box-shadow, transform;
        }
        img:hover,
        img:focus,
        img:active {
          -webkit-transform: scale(1.1);
          transform: scale(1.2);
        }
      }
    }
  }

