@-webkit-keyframes flare {
  100% {
    -webkit-transform: translateY(-20px) scale(1.5);
    transform: translateY(-20px) scale(1.5);
    -webkit-filter: blur(10px);
    filter: blur(10px);
    opacity: 0;
  }
}

@keyframes flare {
  100% {
    -webkit-transform: translateY(-20px) scale(1.5);
    transform: translateY(-20px) scale(1.5);
    -webkit-filter: blur(10px);
    filter: blur(10px);
    opacity: 0;
  }
}

@-webkit-keyframes burn {
  0% {
    color: #ff826e;
    background: #f0b607;
    box-shadow: 0 0 5px 0 #c8000a, 0 0 5px 0 rgba(230, 30, 10, 0.8), 0 0 5px 0 rgba(230, 230, 10, 0.6);
  }

  100% {
    color: black;
    background: black;
    box-shadow: 0 -35px 40px 30px rgba(255, 181, 10, 0), 0 -30px 30px 10px rgba(235, 179, 12, 0), 0 -20px 10px 0 rgba(228, 199, 12, 0.912);
  }
}

@keyframes burn {
  0% {
    color: #ff826e;
    background: #FFE383;
    box-shadow: 0 0 5px 0 #c8000a, 0 0 5px 0 rgba(230, 30, 10, 0.8), 0 0 5px 0 rgba(230, 230, 10, 0.6);
  }

  100% {
    color: black;
    background: #FFE383;
    box-shadow: 0 -35px 40px 30px rgba(255, 130, 10, 0), 0 -30px 30px 10px rgba(230, 30, 10, 0), 0 -20px 10px 0 rgba(255, 255, 10, 0);
  }
}


@keyframes bounce {
  0% {
    transform: translate3d(0, 0, 0);
    text-shadow: rgba(255, 255, 255, 0.4) 0 0 0.05em;
  }

  100% {
    transform: translate3d(0, -0.3em, 0);
    text-shadow: rgba(255, 255, 255, 0.4) 0 1em 0.35em;
  }
}